
.movie-wrapper {
  margin-top: 45px;
  width: 100%;
  background-color: #f6f6f6;
}
.movie-wrapper .movie-container {
  width: 100%;
}
.movie-wrapper .movie-container .movie-content {
  margin-bottom: 10px;
  width: 100%;
  background: #fff;
  line-height: 1.3em;
  box-shadow: 0 0.0625em 0 0 rgba(230,230,230,0.5);
  text-shadow: 0 1px 0 #f3f3f3;
}
.movie-wrapper .movie-container .movie-content .movie-page-imgWrapper {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  height: 18.5em;
  width: 100%;
  background-image: url("./movie-list-background.png");
  background-size: 100% 100%;
}
.movie-wrapper .movie-container .movie-content .movie-page-imgWrapper .movie-page-imgContainer {
  width: 24em;
  text-shadow: 0 1px 0 #f3f3f3;
}
.movie-wrapper .movie-container .movie-content .movie-page-imgWrapper .movie-page-imgContainer img {
  width: 100%;
  height: auto;
  border-radius: 8px;
}
.movie-wrapper .movie-container .movie-content p {
  padding-left: 5%;
  padding-top: 20px;
  opacity: 0.4;
  color: #000;
}
.movie-wrapper .movie-container .movie-content .movie-page-index {
  text-align: center;
  line-height: 1.85em;
  font-size: 0.875em;
}
.movie-wrapper .movie-container .movie-content .movie-page-title {
  opacity: 0.55;
  line-height: 1.4em;
  font-size: 1.375em;
}
.movie-wrapper .movie-container .movie-content .movie-page-author {
  padding-bottom: 10px;
  line-height: 1.3em;
  font-size: 1em;
}
.movie-wrapper .movie-container .movie-content .movie-page-forward,
.movie-wrapper .movie-container .movie-content .movie-page-subtitle {
  line-height: 1em;
  font-size: 0.875 emem;
}
.movie-wrapper .movie-container .movie-content .movie-page-subtitle {
  margin-right: 33px;
  text-align: right;
}
.movie-wrapper .movie-container .movie-content .movie-page-date {
  padding-bottom: 20px;
  line-height: 1.4em;
  font-size: 0.625em;
}


