
#app .header-wrapper {
  display: block;
  position: fixed;
  top: 0;
  width: 100%;
  height: 44px;
  z-index: 99;
  background: #fff;
  box-shadow: 0px 2px 10px rgba(0,0,0,0.1);
}
#app .header-wrapper .header {
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 100%;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
}
#app .header-wrapper .header :nth-child(1) {
  color: rgba(0,0,0,0.3);
  margin-left: 5px;
}
#app .header-wrapper .header p {
  color: rgba(0,0,0,0.3);
  font-family: "PingFang SC";
  font-size: 18px;
}
#app .header-wrapper .header :nth-child(3) {
  margin-right: 10px;
}
#app .mint-popup.mint-popup-left {
  width: 188px;
  height: 100%;
  border-right: 1px solid #eee;
  box-shadow: 0 1px 4px rgba(0,0,0,0.27), 0 0 40px rgba(0,0,0,0.06);
}
#app .mint-popup.mint-popup-left .nav-left li {
  margin-top: 30px;
  height: 28px;
  text-align: center;
  vertical-align: middle;
  line-height: 28px;
  font-size: 16px;
  font-family: sans-serif;
  color: rgba(0,0,0,0.6);
}
#app .v-modal {
  opacity: 0;
}


