
.AppDownload-wrapper {
  margin-top: 45px;
  background: #f3f3f3;
  width: 100%;
  min-height: 726px;
  overflow: hidden;
}
.AppDownload-wrapper .AppDownload-container {
  margin-top: 150px;
  width: 100%;
  text-align: center;
}
.AppDownload-wrapper .AppDownload-container img {
  max-width: 129px;
}
.AppDownload-wrapper .AppDownload-container strong {
  display: list-item;
  margin-top: 15px;
  line-height: 1.3;
  font-size: 14px;
  font-weight: 100;
}
.AppDownload-wrapper .AppDownload-container p {
  margin-top: 15px;
  line-height: 1.3;
  font-size: 13px;
  color: rgba(0,0,0,0.15);
  text-shadow: 0 1px 0 0 1px 0 #f3f3f3;
}


